.MobileNavbarLinks__wrapper {
  display: none;
}

@media screen and (max-width: 700px) {
  .MobileNavbarLinks__wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 32px;
    gap: 50px;
    background: var(--color-white-lilac);

    &__link1,
    &__link2,
    &__link3 {
      font-style: normal;
      font-weight: 600;
      font-size: 14px;
      line-height: 17px;
      color: var(--color-bunting);
      text-decoration: none;
    }

    &__button {
      text-decoration: none;
      display: flex;
      justify-content: center;
      padding: 15.5px 24px;
      color: var(--color-bunting);
      background-color: var(--color-white);
      border: 2px solid var(--color-blue-ribbon);
      width: 100%;
      height: 48px;
      border-radius: 8px;
      font-size: 14px;
      font-weight: 700;
      line-height: 1em;
    }
  }
}
